import * as R from 'ramda';
import {
  getDataflow,
  getPeriod,
  getLastnobservations,
  getLayout,
  getTimeDimensionInverted,
} from '../selectors/vis';
import { getLocale } from '../selectors/app';
import { getDataquery } from '../selectors/sdmx';
import sdmxApi from '../api/sdmx';
import { OVERRIDE_DATAQUERY } from './vis';
import { setPending, pushLog, LOG_ERROR } from './app';
import { getDimensions } from '../selectors/sdmx';

const isDev = process.env.NODE_ENV === 'development';

//-----------------------------------------------------------------------------------------constants
export const HANDLE_STRUCTURE = '@@sdmx/HANDLE_STRUCTURE';
export const HANDLE_DATA = '@@sdmx/HANDLE_DATA';
export const HANDLE_DATAFLOWS = '@@sdmx/HANDLE_DATAFLOWS';
export const HANDLE_HIERARCHY = '@@sdmx/HANDLE_HIERARCHY';

//--------------------------------------------------------------------------------------thunks (api)
const request = (dispatch, ctx) => {
  const { method } = ctx;
  const pendingId = ctx.pendingId || method;

  // eslint-disable-next-line no-console
  if (isDev) console.info(`request: ${pendingId}`);

  dispatch(setPending(pendingId, true));
  return sdmxApi(ctx)
    .then(res => {
      dispatch(setPending(pendingId));
      return res;
    })
    .catch(error => {
      const log = error.response
        ? { method, errorCode: error.response.data.errorCode, statusCode: error.response.status }
        : { method, error };

      dispatch(setPending(pendingId));
      dispatch(pushLog({ type: LOG_ERROR, payload: { log } }));

      // required to break the promised chain
      throw error;
    });
};

/*
This method tries to fix a wrong dataquery param:
The user selected a code that is not available in the dimension, instead of showing a "No data" error it removes the code from the selection
*/
const cleanDataQueryAgainstStructure = (dataquery, dims) => {
  if (!dataquery || dataquery == '') return dataquery;

  // R.getIndex

  let dqParts = dataquery.split('.');

  for (let i = 0; i < dqParts.length; i++) {
    if (dims[i] && dims[i].forced && dims[i].forced != null) dqParts[i] = dims[i].forced;

    if (dqParts[i] == '') continue;
    if (!dims[i] || !dims[i].values) continue; //Just to avoid breaking if some dots are accidentally added at the end
    var dimCodes = dqParts[i].split('+');

    // const dimCodes = dqParts[i].split('+');
    for (let c = dimCodes.length - 1; c >= 0; c--) {
      const found = dims[i].values.some(code => code.id == dimCodes[c]);
      if (!found) {
        dimCodes.splice(c, 1);
      }
    }
    dqParts[i] = dimCodes.join('+');
  }

  return dqParts.join('.');
};

export const requestStructure = ({ shouldRequestStructure }) => (dispatch, getState) => {
  if (R.not(shouldRequestStructure)) return Promise.resolve();

  const configArgs = {
    locale: getLocale(getState()),
    dataflow: getDataflow(getState()),
  };
  return request(dispatch, { method: 'getStructure', configArgs }).then(structure => {
    dispatch({ type: HANDLE_STRUCTURE, structure });
  });
};

export const requestData = ({ shouldRequestStructure } = {}) => (dispatch, getState) => {
  requestStructure({ shouldRequestStructure })(dispatch, getState).then(() => {
    const requestArgs = {
      startPeriod: R.head(getPeriod(getState())),
      endPeriod: R.last(getPeriod(getState())),
      lastnobservations: getLastnobservations(getState()),
    };
    const dataquery = cleanDataQueryAgainstStructure(
      getDataquery(getState()),
      getDimensions(getState()),
    );
    const dataflow = getDataflow(getState());
    const configArgs = {
      locale: getLocale(getState()),
      dataflow,
      dataquery,
    };

    const dims = getDimensions(getState());

    dispatch({ type: OVERRIDE_DATAQUERY, dataquery });
    return request(dispatch, { method: 'getData', configArgs, requestArgs, dims })
      .then(({ data, layout }) => {
        // UNICEF specific
        document.getElementById('root').dispatchEvent(
          new CustomEvent('unicef_dataflowLoaded', {
            detail: {
              dataflow,
              data,
              dataquery,
              requestArgs,
            },
          }),
        );

        dispatch({ type: HANDLE_DATA, data, layout });
        document.getElementById('root').dispatchEvent(new CustomEvent('data_loaded'));
      })
      .catch(error => {
        let nodata = null;
        let nolayout = null;
        dispatch({ type: HANDLE_DATA, nodata, nolayout });
      });
  });
};

export const requestDataFile = ({ isDownloadAllData, dataflow } = {}) => (dispatch, getState) => {
  const requestArgs = isDownloadAllData
    ? {}
    : {
        startPeriod: R.head(getPeriod(getState())),
        endPeriod: R.last(getPeriod(getState())),
        lastnobservations: getLastnobservations(getState()),
      };
  const configArgs = R.cond([
    [R.always(R.not(isDownloadAllData)), R.assoc('dataquery', getDataquery(getState()))],
    [R.T, R.identity],
  ])({
    locale: getLocale(getState()),
    dataflow,
  });
  const pendingId = R.ifElse(
    R.has('id'),
    R.pipe(R.prop('id'), id => `getDataFile/${id}`),
    R.always('requestingDataFile'),
  )(dataflow);
  return request(dispatch, { method: 'getDataFile', configArgs, requestArgs, pendingId });
};

export const requestExcelDataFile = ({ isDownloadAllData, dataflow } = {}) => (
  dispatch,
  getState,
) => {
  const requestArgs = isDownloadAllData
    ? {}
    : {
        startPeriod: R.head(getPeriod(getState())),
        endPeriod: R.last(getPeriod(getState())),
        lastnobservations: getLastnobservations(getState()),
      };
  const configArgs = R.cond([
    [R.always(R.not(isDownloadAllData)), R.assoc('dataquery', getDataquery(getState()))],
    [R.T, R.identity],
  ])({
    locale: getLocale(getState()),
    dataflow,
  });
  const pendingId = R.ifElse(
    R.has('id'),
    R.pipe(R.prop('id'), id => `getDataFile/${id}`),
    R.always('requestingDataFile'),
  )(dataflow);
  const layout = getLayout(getState());
  const timeDimensionInverted = getTimeDimensionInverted(getState())['TIME_PERIOD'];

  return request(dispatch, {
    method: 'getExcelDataFile',
    configArgs,
    requestArgs,
    pendingId,
    layout,
    timeDimensionInverted,
  });
};

export const requestDataflows = () => (dispatch, getState) => {
  const configArgs = {
    locale: getLocale(getState()),
    dataflow: getDataflow(getState()),
  };
  return request(dispatch, { method: 'getDataflows', configArgs }).then(dataflows => {
    dispatch({ type: HANDLE_DATAFLOWS, dataflows });
  });
};

export const requestHierachy = h => (dispatch, getState) => {
  const configArgs = h;
  return request(dispatch, { method: 'getHierarchy', configArgs }).then(hierarchy => {
    dispatch({ type: HANDLE_HIERARCHY, hierarchy });
  });
};

//-------------------------------------------------------------------------------------------reducer
export default (state = {}, action = {}) => {
  switch (action.type) {
    case HANDLE_STRUCTURE:
      return R.set(R.lensProp('dimensions'), R.path(['structure', 'dimensions'], action), state);
    case HANDLE_DATA:
      return R.pipe(
        R.set(R.lensProp('data'), R.prop('data', action)),
        R.set(R.lensProp('layout'), R.prop('layout', action)),
      )(state);
    case HANDLE_DATAFLOWS:
      return R.set(R.lensProp('dataflows'), R.prop('dataflows', action), state);
    case HANDLE_HIERARCHY:
      return R.set(R.lensProp('hierarchy'), R.prop('hierarchy', action), state);
    default:
      return state;
  }
};
