import * as R from 'ramda';
import {
  CHANGE_DATAQUERY,
  CHANGE_PERIOD,
  CHANGE_DATAFLOW,
  CHANGE_LASTNOBS,
  LOAD_DATAFLOWS,
  LOAD_HIERARCHY,
} from '../ducks/vis';
import { requestData, requestDataflows, requestHierachy } from '../ducks/sdmx';

const isDev = process.env.NODE_ENV === 'development';

export const requestDataMiddleware = ({ dispatch }) => next => action => {
  const future = next(action);

  if (
    R.pipe(R.prop('type'), R.flip(R.includes)([CHANGE_DATAQUERY, CHANGE_PERIOD, CHANGE_LASTNOBS]))(
      action,
    )
  ) {
    // eslint-disable-next-line no-console
    if (isDev) console.info(`requestDataMiddleware: ${action.type}`);

    dispatch(requestData());
  }

  if (R.pipe(R.prop('type'), R.flip(R.includes)([CHANGE_DATAFLOW]))(action)) {
    // eslint-disable-next-line no-console
    if (isDev) console.info(`requestStructureMiddleware: ${action.type}`);

    dispatch(requestData({ shouldRequestStructure: true }));
  }

  if (action.type == LOAD_DATAFLOWS) {
    if (isDev) console.info(`requestStructureMiddleware: ${action.type}`);
    dispatch(requestDataflows());
  }

  if (action.type == LOAD_HIERARCHY) {
    if (isDev) console.info(`requestHierarchyMiddleware: ${action.type}`);
    dispatch(requestHierachy(action.payload));
  }

  return future;
};
