import React from 'react';
import { FormattedMessage } from 'react-intl';
import { getHelpUrl } from '../../lib/settings';

export default ({ requestDataFile, requestExcelDataFile, dataflow }) => {
  const actions = [
    {
      icon: 'import',
      id: 'download',
      label: <FormattedMessage id="de.visualisation.toolbar.action.download" />,
      children: [
        {
          id: 'excel-selection',
          label: <FormattedMessage id="de.visualisation.toolbar.action.download.excel.selection" />,
          action: () => requestExcelDataFile({ dataflow }),
        },
        {
          id: 'csv-selection',
          label: <FormattedMessage id="de.visualisation.toolbar.action.download.csv.selection" />,
          action: () => requestDataFile({ dataflow }),
        },
        {
          id: 'csv-full',
          label: <FormattedMessage id="de.visualisation.toolbar.action.download.csv.all" />,
          action: () => requestDataFile({ isDownloadAllData: true, dataflow }),
        },
      ],
    },
  ];

  /*
  if (R.and(!R.isNil(getHelpUrl), !R.equals('', getHelpUrl))) {
    actions.push({
      icon: 'help',
      id: 'help',
      label: <FormattedMessage id="de.visualisation.toolbar.action.help" />,
      action: () => {
        window.location = getHelpUrl;
      },
    });
  }
*/
  return actions;
};
