import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import {
  DELayout,
  DELayoutContent,
  DELayoutSide,
  DELayoutMain,
  VXLoader,
  VXFilterContainerCollapsible,
} from '@sis-cc/dotstatsuite-ui-components';
import NarrowFilters from '../NarrowFilters';
import FilterPeriod from '../FilterPeriod';
import FilterSelection from '../FilterSelection';
import Filters from '../Filters';
import TableConfig from '../TableConfig';
import Main from '../Main';
import Lastnobs from '../Lastnobs';
import DataflowNavigator from '../DataflowNavigator';
import HierarchicalCodelist2 from '../HierarchicalCodelist';

const App = ({
  isNarrow,
  isRtl,
  isLoadingStructure,
  isLoadingData,
  isFullscreen,
  lastnobs_active,
}) => (
  <DELayout isNarrow={isNarrow} isRtl={isRtl} isFull={isFullscreen}>
    <DELayoutContent>
      {isLoadingStructure && (
        <VXLoader
          isNarrow={isNarrow}
          isRtl={isRtl}
          loadingLabel={<FormattedMessage id="de.visualisation.loading" />}
          isLarge
        />
      )}
    </DELayoutContent>
    {!isLoadingStructure && (
      <DELayoutSide>
        {isNarrow && (
          <NarrowFilters isNarrow={isNarrow} isRtl={isRtl} lastnobs_active={lastnobs_active} />
        )}

        {!isNarrow && (
          <div>
            <Lastnobs />
            <div style={lastnobs_active ? { opacity: 0.4, pointerEvents: 'none' } : {}}>
              <FilterPeriod isNarrow={isNarrow} isRtl={isRtl} />
            </div>
            <FilterSelection isNarrow={isNarrow} isRtl={isRtl} />
            <Filters isNarrow={isNarrow} isRtl={isRtl} />

            <HierarchicalCodelist2 isCollapsible={true} />

            <div className="TableConfig_wrapper">
              <VXFilterContainerCollapsible
                titleLabel={<FormattedMessage id="de.table.layout.title" />}
                isOpen={false}
                noScroll={true}
              >
                {!isLoadingData && <TableConfig isRtl={isRtl} />}
              </VXFilterContainerCollapsible>
            </div>

            <DataflowNavigator
              stickyDims={['REF_AREA', 'INDICATOR', 'SEX']}
              dataflowsOnEvidence={[
                { id: 'GLOBAL_DATAFLOW', actionText: 'Go to the Global Dataflow' },
              ]}
            />
            {/* <div className="TableConfig_wrapper" /> */}
          </div>
        )}
      </DELayoutSide>
    )}
    {!isLoadingStructure && (
      <DELayoutMain>
        <Main isNarrow={isNarrow} isRtl={isRtl} isFullscreen={isFullscreen} />
      </DELayoutMain>
    )}
  </DELayout>
);

App.propTypes = {
  isNarrow: PropTypes.bool,
  isRtl: PropTypes.bool,
  isLoadingStructure: PropTypes.bool,
  isLoadingData: PropTypes.bool,
  isFullscreen: PropTypes.bool,
  lastnobs_active: PropTypes.bool,
};

export default App;
