//Enque the unit multipliers to the unit. Hack to avoid altering the dotstatsuite-ui-header project
const addUnitMultiplierToUnit = (headerObj, footnotes) => {
  //If the uprs(contains the unit) is empty return (the unit is not the same for the data shown)
  if (headerObj.uprs == '') return headerObj;

  let newUprs = headerObj.uprs;

  //Append the multiplier if the unit is the same for all the data shown.
  footnotes.footnotes.forEach(elem => {
    if (elem.id == 'UNIT_MULTIPLIER' && elem.isFootnote) {
      const mult = elem.values
        .map(e => {
          return e.name;
        })
        .join(', ');
      newUprs = newUprs + ' (' + mult + ')';
    }
  });
  headerObj.uprs = newUprs;
  return headerObj;
};

export default compose(
  connect(
    createStructuredSelector({
      dimensions: getVisDataDimensions(),
      customAttributes: getCustomAttributes,
      layoutIds: getVisTableLayout(),
      itemFormatter: getDimensionFormat,
      data: getVisTableData(),
      variant: getDimensionGetter,
      limited: getLimited(),
      selItems: getSelection,
    }),
  ),
  mapProps(
    ({
      chartData,
      viewerConfig,
      data,
      customAttributes,
      dimensions,
      layoutIds,
      itemFormatter,
      variant,
      limited,
      selItems,
    }) => ({
      viewerConfig,
      data,
      layoutIds,
      itemFormatter,
      renderObservation: rules.observationGetter(Cell)({
        observations: R.prop('observations', data),
        dimensions,
        customAttributes,
        isRtl: false,
      })(R.identity),
      // little dirty for now, in the wait of a better common mgmt of table and charts
      //header: R.merge(R.pick(['title', 'subtitle', 'uprs'], chartData),R.pick(['footnotes'],customAttributes)),
      header: addUnitMultiplierToUnit(
        R.pick(['title', 'subtitle', 'uprs'], chartData),
        R.pick(['footnotes'], customAttributes),
      ),
      footnotes: R.prop('footnotes', chartData),
      isNotATable: R.pipe(R.prop('observations'), R.values, R.length, R.equals(1))(data),
      variant,
      limited,
      selItems,
    }),
  ),
)(Table);
